import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, PanelHeader, Button } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/style.css';
import * as vkSelectors from '../store/vk/reducer';

//example import * as currencyRatesActions from '../store/currency_rates/actions';

import pets from '../image/pets.svg';
import MainHeader from './MainHeader';

class MainPanel extends Component {


    componentWillMount() {
        //this.props.dispatch(currencyRatesActions.fetchUsdEurRate()); вызов api get
    }

    openNext = () => {
        this.props.router.navigate('choice');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={() => {}} />
                <Div style={{
                    height: 'calc(100vh - 85px)',
                    display: 'flex',
                    flexDirection: 'column',
                    justifyContent: 'center'
                }}>
                    <Div style={{
                        textAlign: 'center', 
                        height: '90%',
                        display: 'flex',
                        flexDirection: 'column',
                        alignItems: 'center',
                        justifyContent: 'space-between'
                    }}>
                        <img style={{marginTop: '20%'}} src={pets} width="240" />
                        <div>
                            <h1 style={{marginTop: 0}}>Здравствуйте, Ник!</h1>
                            <div style={{ fontSize: '17px', lineHeight: '20px'}}>С приложением "Лапы и хвосты" следить за здоровьем Вашего питомца легко и просто!</div>
                        </div>
                        <Button size="xl" component="a" onClick={this.openNext}>Продолжить</Button>
                    </Div>
                </Div>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(MainPanel);
