import React, {Component} from 'react';
import { connect } from 'react-redux';
import { ConfigProvider, Root, View } from '@vkontakte/vkui';

import { RouteNode } from 'react-router5'
import '@vkontakte/vkui/dist/vkui.css';

import * as vkSelectors from '../store/vk/reducer';
import * as vkActions from '../store/vk/actions';


import MainPanel from './MainPanel';
import ChoicePanel from './ChoicePanel';
import InputPanel from './InputPanel';
import ConsentPanel from './ConsentPanel';

class App extends Component {

    componentWillMount() {
        this.props.dispatch(vkActions.initApp());
    }

    render() {
        const arrayPanel = {
            main: 'mainPanel',
            city: 'cityPanel',
            choice: 'choicePanel',
            input: 'inputPanel',
            consent: 'consentPanel'
        };

        let activePanel = arrayPanel[this.props.route.name];
        
        return (
            <ConfigProvider insets={this.props.insets}>
                <Root activeView="mainView">
                    <View id="mainView" activePanel={activePanel}>
                        <MainPanel router={this.props.router} id="mainPanel" />
                        <ChoicePanel router={this.props.router} id="choicePanel"  />
                        <InputPanel router={this.props.router} id="inputPanel"  />
                        <ConsentPanel router={this.props.router} id="consentPanel"  />
                    </View>
                </Root>
            </ConfigProvider>
        );
    }
}

function mapStateToProps(state) {
    return {
        accessToken: vkSelectors.getAccessToken(state),
        insets: vkSelectors.getInsets(state),
    };
}

export default connect(mapStateToProps)(
    (props) => (
        <RouteNode nodeName="">
            {({ route }) => <App route={route} {...props}/>}
        </RouteNode>
    )
);
