import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, PanelHeader, Input, Button } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/style.css';
import * as vkSelectors from '../store/vk/reducer';

//example import * as currencyRatesActions from '../store/currency_rates/actions';

import MainHeader from './MainHeader';

class InputPanel extends Component {

    componentWillMount() {
        //this.props.dispatch(currencyRatesActions.fetchUsdEurRate());
    }

    openNext = () => {
        this.props.router.navigate('consent');
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Div>
                    <Div style={{textAlign: 'center'}}>
                        <h1>Как зовут Вашего питомца</h1>
                        <Input type="text" placeholder="Введите кличку" />
                    </Div>
                    <Div style={{textAlign: 'center'}}>
                        <h1>Введите дату рождения</h1>
                        <Input type="date" />
                    </Div>
                    <Div>
                        <Button size="xl" component="a" onClick={this.openNext}>Продолжить</Button>
                    </Div>
                </Div>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(InputPanel);
