import React, { Component } from 'react';
import { connect } from 'react-redux';
import { Panel, Div, PanelHeader, Button } from '@vkontakte/vkui';
import '@vkontakte/vkui/dist/vkui.css';
import '../css/style.css';
import * as vkSelectors from '../store/vk/reducer';

//example import * as currencyRatesActions from '../store/currency_rates/actions';

import MainHeader from './MainHeader';

class ConsentPanel extends Component {

    componentWillMount() {
        //this.props.dispatch(currencyRatesActions.fetchUsdEurRate());
    }

    openNext = () => {
        //this.props.router.navigate('consent');
    }

    openMain = () => {
        this.props.router.navigate('main');
    }

    render() {
        return (
            <Panel id={this.props.id}>
                <MainHeader openMain={this.openMain} />
                <Div>
                    <Div style={{textAlign: 'center'}}>
                        <h1>Хотите добавить еще питомца?</h1>
                    </Div>
                    <Div>
                        <Div style={{ margin: '0 auto', width: '150px', display: 'flex', justifyContent: 'space-between' }}>
                            <Button>Да</Button>
                            <Button onClick={this.openNext}>Нет</Button>
                        </Div>
                    </Div>
                </Div>
            </Panel>
        );
    }
}

function mapStateToProps(state) {
    return {
        notificationStatus: vkSelectors.getNotificationStatus(state),
    };
}

export default connect(mapStateToProps)(ConsentPanel);
